





















import Vue from 'vue'
import { getUserInfo } from '@/services/user'
export default Vue.extend({
    name: 'app-header',
    data () {
        return {
            userInfo: {},
        }
    },
    created () {
        this.loadUserInfo()
    },
    methods: {
        async loadUserInfo () {
            const { data } = await getUserInfo()
            this.userInfo = data.content
        },
        handleLogout () {
            this.$confirm('确认是否退出, 是否继续?', '退出提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                // 清除登录状态 跳转到登录页面
                this.$store.commit('setUser', null)
                this.$router.push('/login')
                this.$message({
                    type: 'success',
                    message: '退出成功!',
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消退出',
                })
            })
        },
    },
})
